.header_container{
    position: fixed;
    width: 100%;
}

.header_area{
    background-color: lightgray;
    display: flex;
    /* border: 1px solid black; */
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
}

.header_logo{
    font-size: 1.5rem;
    font-weight: bold;
}

.header_icon{
    font-size: 1.5rem;
    cursor: pointer;
}
.header_icon:hover{
    color: #989898;
}

.header_icon_group{
    display: flex;
    gap: 10px;
}

.list_area{
    height: 100vh;
    width: 20%;
    background-color: lightgray;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.list_area span{
    cursor: pointer;
}
.list_area span:hover{
    color: #989898;
}